import React from "react"
import { Link } from "gatsby"
import FocusTrap from "focus-trap-react"
import Logo from "./common/Logo"
import Button from "./common/Button"

import { IconClose, IconArrowShort, IconUser } from "./common/icons"

const MenuItem = ({ link, number, name }) => (
  <Link to={link} className="flex items-start">
    <span className="text-xs font-mono text-primary mr-2 pt-1">{number}</span>
    {name}
  </Link>
)

const SocialItem = ({ link, name }) => (
  <a href={link} className="font-light text-xl">
    {name}
  </a>
)

export default ({ isMenuOpened, setIsMenuOpened }) => {
  return isMenuOpened ? (
    <FocusTrap active={isMenuOpened}>
      <div className="fixed inset-0 overflow-y-auto">
        <div className="h-screen min-h-560 xs:min-h-620 pt-32 bg-n-800 flex flex-col justify-between">
          <div className="fixed top-0 px-9 md:px-28 h-20 w-full bg-n-800 flex justify-between items-center">
            <Logo />
            <button onClick={() => setIsMenuOpened(false)}>
              <IconClose className="text-md" />
            </button>
          </div>
          <div className="flex-1 px-9 md:px-28 w-full box-border flex flex-col justify-center text-4xl space-y-10">
            <MenuItem link="/" number="01" name="About" />
            <MenuItem link="/" number="02" name="FAQ" />
            <MenuItem link="/" number="03" name="Contact" />
          </div>
          <div className="pb-16 px-9 md:px-28 w-full box-border flex flex-col space-y-1">
            <span className="uppercase font-mono text-primary text-2xs leading-7">
              Follow
            </span>
            <SocialItem link="/" name="Facebook" />
            <SocialItem link="/" name="Instagram" />
            <SocialItem link="/" name="Twitter" />
          </div>
          <div>
            <Button type="defaultSquare" className="py-5">
              Apply for Account
              <IconArrowShort className="ml-3 relative bottom-px text-90" />
            </Button>
            <Button type="primarySquare" className="py-5">
              <IconUser className="mr-3 relative bottom-px text-xs" />
              Sign In
            </Button>
          </div>
        </div>
      </div>
    </FocusTrap>
  ) : null
}
