import React from "react"
import { StaticImage } from "gatsby-plugin-image"
import Button from "./common/Button"
import Link from "./common/Link"
import MobileMenu from "./MobileMenu"
import Logo from "./common/Logo"

import {
  IconArrowLong,
  IconArrowShort,
  IconBell,
  IconMenu,
  IconUser,
} from "./common/icons"

const HeroImage = ({ height }) => (
  <>
    <StaticImage
      src="../images/hero-image.png"
      alt=""
      style={{ width: "100%", height }}
      layout="fixed"
      objectPosition="top center"
      quality={90}
      placeholder="blurred"
      loading="eager"
    />
    <div className="relative -top-3 h-3 bg-gradient-to-b from-transparent to-n-800" />
  </>
)

const Main = () => {
  const [isMenuOpened, setIsMenuOpened] = React.useState(false)

  React.useEffect(() => {
    document.body.classList.toggle("overflow-hidden", isMenuOpened)
    return () => document.body.classList.remove("overflow-hidden")
  }, [isMenuOpened])

  return (
    <div className="min-h-screen relative bg-n-800 text-n-200">
      <div className="absolute inset-0 md:hidden">
        <HeroImage height="60%" />
      </div>
      <div className="absolute inset-0 hidden md:block">
        <HeroImage height="100%" />
      </div>
      <div className="relative h-screen min-h-480 xs:min-h-560 flex flex-col justify-between children:flex-shrink-0">
        <div className="h-20 px-9 md:px-28 flex justify-between items-center">
          <Logo />
          <div className="hidden md:block space-x-11 text-sm font-book">
            <a href="/">About</a>
            <a href="/">FAQ</a>
            <a href="/">Contact</a>
            <span>
              <IconMenu className="text-70" />
            </span>
            <a href="/">
              <span className="w-1.5 h-1.5 bg-primary inline-block align-middle rounded-full mr-2.5" />
              Sign In
            </a>
          </div>
          <div className="md:hidden text-sm font-book">
            <button onClick={() => setIsMenuOpened(true)}>
              <IconMenu className="text-70" />
            </button>
          </div>
        </div>
        <div className="px-9 sm:px-28 space-y-14">
          <div className="max-w-480 text-4xl md:text-4.5xl font-medium">
            The invite-only platform for{" "}
            <span className="text-primary">
              Musicians, Labels and Youtubers.
            </span>
          </div>
          <div className="flex space-x-4">
            <Button type="secondary">
              Apply for Account
              <IconArrowShort className="ml-3 relative bottom-px text-90" />
            </Button>
            <Button>
              <IconUser className="mr-3 relative bottom-px text-xs" />
              Sign In
            </Button>
          </div>
        </div>
        <a href="/" className="absolute left-8 bottom-20">
          <IconArrowLong className="transform rotate-90" />
        </a>
        <div className="bg-secondary font-mono text-2xs">
          <div className="container min-h-11 flex items-center">
            <div className="w-9 flex-shrink-0">
              <IconBell className="text-sm" />
            </div>
            <div>
              We Just Launched a new platform called Elite Alliance Music for
              indie artists.{" "}
              <Link href="/" className="text-primary">
                Visit Here
              </Link>
            </div>
          </div>
        </div>
      </div>
      <MobileMenu
        isMenuOpened={isMenuOpened}
        setIsMenuOpened={setIsMenuOpened}
      />
    </div>
  )
}

export default Main
