import React from "react"

const config = {
  default: "bg-n-200 text-n-800 border-transparent font-bold rounded-full",
  primary: "bg-primary text-n-800 border-transparent rounded-full",
  secondary: "bg-secondary text-n-100 border-transparent rounded-full",
  white: "bg-n-100 text-n-800 border-n-300 font-bold rounded-full",
  grey: "bg-n-300 text-n-800 border-transparent font-bold rounded-full",
  black: "bg-n-900 text-n-200 border-transparent font-bold rounded-full",
  defaultSquare: "bg-n-200 text-n-800 font-bold border-transparent w-1/2",
  primarySquare: "bg-primary text-n-800 font-bold border-transparent w-1/2",
}

const Button = ({ type = "default", children, className, ...props }) => (
  <button
    className={`font-mono text-2xs py-3 px-7 whitespace-nowrap border focus:outline-none focus:ring-4 ${
      config[type]
    } ${className || ""}`}
    {...props}
  >
    {children}
  </button>
)

export default Button
